body {
  flex-direction: column;
  align-items: center;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  transition: transform .3s;
  display: flex;
}

* {
  box-sizing: border-box;
}

h2, p {
  margin: 0;
}

.product-card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  transition: transform .3s;
}

.product-card:hover {
  color: #34568b;
  transition: transform .3s;
  transform: scale(1.2);
}

.product-card__description {
  color: #060b35;
  margin: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover .product-card__description {
  color: #34568b;
  transition: color .3s;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
  display: block;
}

.product-card__product-code {
  color: #616070;
  margin: 0 16px 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rate {
  justify-content: space-between;
  align-items: center;
  margin: 0 16px 24px;
  display: flex;
}

.product-card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin: 0 16px 16px;
  display: flex;
}

.product-card__text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product-card__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 0 16px 16px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: background-color .3s;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo, .header__image {
  width: 40px;
  height: 40px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(1, 200px);
  place-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
  transition: color .3s;
}

.nav__link--active {
  color: #00acdc;
  position: relative;
}

.nav__link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

