.header {
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  background-color: $white-accent;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);

  &__logo {
    height: 40px;
    width: 40px;
  }

  &__image {
    width: 40px;
    height: 40px;
  }
}


